export default async function apiFetch(formdata) {
  const inputUrl = formdata.get('text');

  const queryString = new URL(inputUrl);
  const fetchUrl = `https://www.youtube.com/oembed?url=https://www.youtube.com/watch${queryString.search}&format=json`;
  const res = await fetch(fetchUrl);
  const data = await res.json();

  return data;
}
